.input-group-text {
	min-height: 50px;
}

.input-container {
	position: relative;
	flex: 1;
}

.input-container input {
	min-height: 50px;
}

.input-container select {
	min-height: 50px;
	-webkit-appearance: none;
	-moz-appearance: none;
	appearance: none;
}

.input-container textarea {
	min-height: 144px;
}

.input-container label {
	position: absolute;
	z-index: 10;
	pointer-events: none;
	padding: 12px;
	transition: 0.2s ease all;
	color: #696969;
}

.input-container input + label,
.input-container select + label {
	left: 0;
	top: 50%;
	transform: translateY(-50%);
}

.input-container textarea + label {
	left: 0;
	top: 0;
}

.input-container input:focus ~ label,
.input-container input:not([value=""]) ~ label {
	top: 12px;
	font-size: 10px;
}

.input-container input:focus,
.input-container input:not([value=""]) {
	padding-top: 18px;
}

.input-container input:focus ~ label {
	color: #000000;
}

.input-container input.input-validation-error:focus ~ label,
.input-container input.input-validation-error:not([value=""]) ~ label {
	color: #f0687a !important;
}

.input-container input::-webkit-datetime-edit,
.input-container input[type=date] {
	color: transparent;
}

.input-container input:focus::-webkit-datetime-edit,
.input-container input[type=date]:focus {
	color: #696969 !important;
}

.input-container input:not([value=""])::-webkit-datetime-edit,
.input-container input[type=date]:not([value=""]) {
	color: #000000;
}

.input-container select,
.input-container select option {
	color: #000000;
}

.input-container select:invalid,
.input-container select option:disabled,
.input-container select option[value=""] {
	color: #999999;
}

.input-container .select-wrapper {
	position: relative;
}

.input-container .select-wrapper::after {
	right: 16px;
	top: 50%;
	transform: translateY(-50%);
	position: absolute;
}

.input-container select:focus ~ label,
.input-container select:not([value=""]) ~ label {
	top: 12px;
	font-size: 10px;
}

.input-container select:focus,
.input-container select:not([value=""]) {
	padding-top: 18px;
}

.input-container select:focus ~ label {
	color: #000000;
}

.input-container select.input-validation-error:focus ~ label,
.input-container select.input-validation-error:not([value=""]) ~ labell {
	color: #f0687a !important;
}

.input-container textarea:focus ~ label,
.input-container textarea:not([value=""]) ~ label {
	top: 12px;
	transform: translateY(-50%);
	font-size: 10px;
}

.input-container textarea:focus,
.input-container textarea:not([value=""]) {
	padding-top: 18px;
}

.input-container textarea:focus ~ label {
	color: #000000;
}

.input-container textarea.input-validation-error:focus ~ label,
.input-container textarea.input-validation-error:not([value=""]) ~ label {
	color: #f0687a !important;
}

.customfile {
	position: relative;
	flex: 1;
	margin-top: 6px;
	width: 100%;
	max-width: 800px;
}

.customfile input {
	position: absolute;
	bottom: 0;
	border: none;
	height: 1px;
	width: 1px;
	padding: 0px;
	margin: 0px;
}

.customfile input:disabled + .customfile-button {
	cursor: default !important;
	border-color: #ececec !important;
	background: #ececec !important;
	color: #b4b4b4 !important;
}

.customfile input:focus {
	outline: none;
	box-shadow: none;
}

.customfile input::file-selector-button {
	visibility: hidden;
}

.customfile .customfile-button-container {
	position: relative;
	height: auto;
}

.customfile .customfile-button-container .customfile-button {
	border: 1px solid #ced4da;
	border-radius: 0.25rem;
	background: white;
	padding: 8px 14px;
	margin-bottom: 0;
	color: #000;
}

.customfile .customfile-button-container .customfile-button:hover {
	border-color: #adadad;
	background: #e6e6e6;
	cursor: pointer;
}

.customfile .customfile-button-container input:focus + .customfile-button {
	border-color: #80bdff;
	outline: 3px solid #bfdeff;
	background: #e6e6e6;
}

.customfile .customfile-button-container .req {
	position: absolute;
	top: 0;
	margin-left: 6px;
}

.customfile .table {
	width: 100%;
	border: 1px solid #ced4da;
	margin-top: 10px;
}

.customfile .table thead {
	background: #e9ecef;
	color: #495057;
}

.customfile .table thead th {
	font-weight: normal;
	padding: 0.5rem;
	border-top: none;
	border-bottom: none;
}

.customfile .table tbody {
	background: white;
}

.customfile .table tbody tr th,
.customfile .table tbody tr td {
	vertical-align: middle;
}

.customfile .table tbody tr td:last-child {
	text-align: right;
}

.customfile .table tbody .customfile-filename {
	max-width: 100px;
	overflow: hidden;
	text-overflow: ellipsis;
	white-space: nowrap;
}

.customfile .table tbody button {
	outline: none;
	border: 1px solid #ced4da;
	border-radius: 0.25rem;
	text-align: center;
	padding: 0;
	height: 40px;
	width: 40px;
}

.customfile .table tbody .customfile-action:hover {
	background: #FFF;
}

.customfile .table tbody .customfile-action:focus {
	border-color: #80bdff;
	outline: 3px solid #bfdeff;
	background: #FFF;
}

.customfile .table tbody .customfile-action-none {
	border: none !important;
}

.customfile .progress-container {
	margin: 5px 0;
	background-color: #e6e6e6;
}

.customfile .progress-container .progress-bar {
	height: 10px;
	width: 0%;
}

.customfile .progress {
	background-color: #727272;
}

